.navigation{width:100%;display:flex;flex-direction:column;padding:0 0 0 50px}.navigation__wrapper{width:100%;display:none;position:fixed;top:0;padding:70px 20px 20px 20px;border-radius:0 0 12px 12px;left:0;background:#4b4949;width:25%;user-select:none}@media screen and (max-width: 660px){.navigation__wrapper{padding:70px 20px 20px 5px;width:40%;font-size:.8em}}.navigation__wrapper ul{list-style-type:none;display:flex;flex-direction:column;align-items:flex-start;margin-left:25%}.navigation__wrapper ul li{padding-bottom:20px}.navigation a{text-decoration:none;color:#999;margin:0 0 8px 0;padding-bottom:20px}.navigation a:hover{color:#e0e0e0}.navigation #burger{display:none}.navigation #burger:checked+label+nav{display:block;animation:show .5s ease}.navigation #burger:checked+.navigation__burger::before{content:"⮾";cursor:pointer}.navigation__burger{display:block;position:fixed;width:30px;text-align:center;left:20px;top:22px;z-index:999}.navigation__burger::before{content:"☰";cursor:pointer}@keyframes show{0%{opacity:0}100%{opacity:1}}